import { TextField } from '@mui/material'

const FieldComponent = ({
  disabled,
  focused,
  fullWidth,
  id,
  label,
  name,
  onChange,
  required,
  sx,
  type,
  value,
}) => {
  return (
    <TextField
      disabled={disabled}
      focused={focused}
      fullWidth={fullWidth}
      id={id}
      label={label}
      name={name}
      onChange={onChange}
      required={required}
      sx={sx}
      type={type}
      value={value}
    />
  )
}

export default FieldComponent
